<template>
  <div class="container">
    <div class="row">
      <div class="col-xs-12">
        <br>
        <h1>Message Details</h1>
        <hr>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-1">
        <router-link :to="{ name: 'merchant', params: { merchantId: getCurrentMessage.friendlyId } }">
          <img :src="getCurrentMessage.image_logo" class="img-fluid" />
        </router-link>
      </div>
      <div class="col-xs-11">
        <router-link :to="{ name: 'merchant', params: { merchantId: getCurrentMessage.friendlyId } }">
          <h3><b>{{ getCurrentMessage.merchant_name }} - {{ getCurrentMessage.branchName }}</b></h3>
          <h5>{{ getCurrentMessage.branchName }}</h5>
        </router-link>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <hr>
        <span v-html="getCurrentMessage.content.replace(/(\||,)/g, '-').replace('N/A', '').replace(/\n/g, '<br>')"></span>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <br>
        <br>
        <button class="btn btn-block btn-primary btn-lg" @click="deleteMessage()">
          <i class="ion-android-delete"></i> Delete message
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { FETCH_MESSAGE, DELETE_MESSAGE } from "@/store/actions.type";

export default {
  name: "viewMessage",
  data() {
    return { 
      
    };
  },

  computed: {
    ...mapGetters(["getCurrentMessage"])
  },

  created() {
    console.log(Date(), 'viewMessage/created', this.$route.params.messageId)
    this.$store.dispatch(FETCH_MESSAGE, this.$route.params.messageId);
  },

  methods: {
    deleteMessage() {

      this.$ga.event('inbox', 'messages', 'delete', 0);

      this.$swal({
        title: 'Delete Message?',
        type: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Keep',
        cancelButtonText: 'Delete',
        showCloseButton: true,
        showLoaderOnConfirm: true
      }).then((result) => {
        if(result.value) {

          this.$ga.event('inbox', 'messages', 'delete', 2);
          
        } else {

          this.$ga.event('inbox', 'messages', 'delete', 1);

          console.log(this.getCurrentMessage)

          this.$store.dispatch(DELETE_MESSAGE, this.$route.params.messageId);

          this.$router.push({ name: 'inbox', })
          
          return 0;
        }
      })

    },
  },
}
</script>

<style scoped>
</style>


