<template>
  <div class="container">
    <div class="row">
      <div class="col-xs-12">
        <br>
        <h1>Inbox</h1>
        <table class="table">
          <tbody>

            <tr @click="openMessage(message.id)" v-for="(message, idx) in getInbox" :key="idx">

              <td>
                <img class="img-fluid merchantImg" :src="message.image_logo" />
              </td>
              <td class="mainColor">
                <small><small>{{ message.dateTime }}</small></small><br>
                <font color="red" v-if="message.isRead === '1'"><i class="ion-load-b"></i></font><b> {{ message.merchant_name }}</b><br>
                {{ message.brief_view }}
              </td>

            </tr>
            
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { FETCH_INBOX } from "@/store/actions.type";

export default {
  name: "viewInbox",
  data() {
    return {
      
    };
  },

  computed: {
    ...mapGetters(["getInbox"])
  },

  created() {
    console.log(Date(), 'Inbox created')
    this.$store.dispatch(FETCH_INBOX);
  },

  methods: {
    openMessage(message) {
      this.$router.push({ name: 'messageDetails', params: { messageId: message }});
    },
  },
};
</script>

<style scoped>
  .img-fluid{
    object-fit: cover;
    width:78px;
    height:78px;
    border-radius: 8px;
  }
  td:first-child{
    width:102px;
    white-space:nowrap;
  }
  .mainColor{
    color: #ffba00;
  }
  .secondaryColor{
    color: #1E1E1E;
  }
  .secondaryColor{
    color: #1E1E1E;
  }
  .secondaryColor{
    color: #1E1E1E;
  }
</style>


