import { InboxService } from "@/common/inbox.service";

import { FETCH_INBOX, FETCH_MESSAGE, DELETE_MESSAGE, } from "@/store/actions.type";

import { SET_INBOX, SET_MESSAGE, } from "@/store/mutations.type";

const initialState = { 
  inbox: [], 
  currentMessage: { },
};
export const state = { ...initialState };


export const actions = {
  async [FETCH_INBOX](context) {
    console.log(Date(), 'Inbox fetching')
    const { data } = await InboxService.query();
    context.commit(SET_INBOX, data.messages);
    return data;
  },

  async [FETCH_MESSAGE](context, id) {
    console.log(Date(), 'FETCH_MESSAGE', id)
    const { data } = await InboxService.get(id);
    context.commit(SET_MESSAGE, data.message);
    return data;
  },

  async [DELETE_MESSAGE](context, id) {
    const { data } = await InboxService.deleteOne(id);
    return data;
  },
};


export const mutations = {
  [SET_INBOX](state, inbox) {
    console.log(Date(), 'Inbox fetched', inbox)
    state.inbox = inbox;
  },

  [SET_MESSAGE](state, message) {
    state.currentMessage = message
  },
};


const getters = {
  getCurrentMessage(state) {
    return state.currentMessage;
  },
  getInbox(state) {
    return state.inbox;
  },
};


export default {
  state,
  actions,
  mutations,
  getters
};



