import ApiService from "@/common/api.service";

export const InboxService = {
  query() {
    return ApiService.query("inbox", { });
	},
	
  get(id) {
    return ApiService.query("inbox/get", {
      params: {
        id: id
      }
    });
  },
	
  deleteOne(id) {
    return ApiService.post("inbox/delete", {
      id: id
    });
  },
	
  deleteAll() {
    return ApiService.query("inbox/delete/all", {  });
  },
};


